import React, { useState } from 'react';
import { Button, TextField, Card, CardContent, Select, MenuItem, Tabs, Tab, Typography, Box } from "@mui/material";
import { Link } from 'react-router-dom';

const Summary = () => {
    const [file, setFile] = useState(null);
    const [summary, setSummary] = useState(null);
  
    const handleFileUpload = (e) => setFile(e.target.files[0]);
    const processDocument = () => {
      setSummary({
        title: "Document Summary",
        themes: [
          { name: "Theme 1", subThemes: ["Sub Theme 1.1", "Sub Theme 1.2"] },
          { name: "Theme 2", subThemes: ["Sub Theme 2.1", "Sub Theme 2.2"] },
        ],
      });
    };
  
    return (
      <Box sx={{ p: 3 }}>
        <input type="file" onChange={handleFileUpload} />
        <Button variant="contained" color="primary" onClick={processDocument} sx={{ ml: 2 }}>Process</Button>
        {summary && (
          <Card sx={{ mt: 2, p: 2 }}>
            <CardContent>
              <Typography variant="h5">{summary.title}</Typography>
              {summary.themes.map((theme, i) => (
                <Box key={i} sx={{ mt: 2 }}>
                  <Typography variant="h6">{theme.name}</Typography>
                  <ul>
                    {theme.subThemes.map((sub, j) => (
                      <li key={j}><Typography>{sub}</Typography></li>
                    ))}
                  </ul>
                </Box>
              ))}
            </CardContent>
          </Card>
        )}
        <Button variant="outlined" sx={{ mt: 2 }}>Download</Button>
        <Button component={Link} to="/chat" variant="contained" color="secondary" sx={{ ml: 2 }}>Chat</Button>
      </Box>
    );
  }

export default Summary;