import React, { useState } from 'react';
import { Button, TextField, Card, CardContent, Select, MenuItem, Tabs, Tab, Typography, Box } from "@mui/material";

const ControlMapping = () => {
    const [files, setFiles] = useState([]);
    const [selectedType, setSelectedType] = useState("");
    const [gridData, setGridData] = useState([]);
  
    const handleFilesUpload = (e) => setFiles([...e.target.files]);
    const handleTypeChange = (e) => {
      setSelectedType(e.target.value);
      setGridData(["Row 1", "Row 2", "Row 3"]);
    };
  
    return (
      <Box sx={{ p: 3 }}>
        <input type="file" multiple onChange={handleFilesUpload} />
        <Select value={selectedType} onChange={handleTypeChange} displayEmpty sx={{ ml: 2 }}>
          <MenuItem value=""><em>Select Type</em></MenuItem>
          <MenuItem value="type1">Type 1</MenuItem>
          <MenuItem value="type2">Type 2</MenuItem>
        </Select>
        <Card sx={{ mt: 2 }}>
          <CardContent>
            <table>
              {gridData.map((row, i) => (
                <tr key={i}><td><Typography>{row}</Typography></td></tr>
              ))}
            </table>
          </CardContent>
        </Card>
        <Button variant="contained" color="primary" sx={{ mt: 2 }}>Download</Button>
      </Box>
    );
  }

export default ControlMapping;