import React, { useState } from 'react';
import { Button, TextField, Card, CardContent, Select, MenuItem, Tabs, Tab, Typography, Box } from "@mui/material";

const Chat = () =>  {
    const [messages, setMessages] = useState([]);
    const [input, setInput] = useState("");
  
    const sendMessage = () => {
      if (input.trim()) {
        setMessages([...messages, { text: input, sender: "user" }]);
        setInput("");
        setTimeout(() => {
          setMessages((prev) => [...prev, { text: "AI Response", sender: "ai" }]);
        }, 1000);
      }
    };
  
    return (
      <Box sx={{ p: 3 }}>
        <Card sx={{ mb: 2 }}>
          <CardContent>
            {messages.map((msg, index) => (
              <Typography key={index} align={msg.sender === "user" ? "right" : "left"}>{msg.text}</Typography>
            ))}
          </CardContent>
        </Card>
        <TextField value={input} onChange={(e) => setInput(e.target.value)} fullWidth placeholder="Type a message..." />
        <Button variant="contained" color="primary" onClick={sendMessage} sx={{ mt: 2 }}>Send</Button>
      </Box>
    );
  }

export default Chat;